.options{
    &__item{
        position: relative;
        margin-top: 8px;
    }
    &__filed{
        width: 100%;
        padding: 8px 30px 8px 12px;
        border: 1px solid #007bff;
        color: #007bff;
        border-radius: 4px;
    }
    &__add{
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;
        justify-content: center;
        position: absolute;
        top: 8px;
        right: 6px;
        font-size: 1.4em;
        padding: 0;
        border: 0;
        background-color: transparent;
        path{
            fill: #007bff
        }
    }
}