.quiz{
    margin-top: 60px;
    background-color: #fff;
    &__length{
        position: relative;
        display: flex;
        flex-direction: row;
        flex-wrap: wrap;
    }
    &__title{
        flex: 1 0 0;
        width: 100%;
        order: 0;
        box-sizing: border-box;
        padding: 6px 10px 6px 0;
        border: 0;
        border-radius: 4px;
        font-size: 2em;
        color: #515458;
    }
    &__count{
        flex: 0 0 20px;
        order: 0;
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;
        justify-content: center;
        font-size: 0.8em;
        text-align: right;
    }
    &__error{
        flex: 1 0 100%;
        order: 1;
        margin-bottom: 15px;
        color: red;
    }
    
    &__field{
        flex: 1 0 0;
        width: 100%;
        box-sizing: border-box;
        padding: 6px 10px 6px 0;
        border: 0;
        border-radius: 4px;
        color: #515458;
    }
    &__discuss{}
    &__question{
        margin-top: 15px;
        padding-right: 0;
    }
    &__bottom{
        margin-top: 30px;
    }
    &__btn{
        float: right;
        padding: 8px 12px;
        border: 0;
        border-radius: 4px;
        background-color: #007bff;
        color: #fff;
        &:disabled{
            background-color: #515458;
        }
    }
    &__success{
        display: flex;
        flex-direction: column;
        flex-wrap: nowrap;
        justify-content: center;
        width: 100%;
        height: 40px;
        margin-top: 30px;
        background-color: #007bff;
        color: #fff;
        font-size: 1.2em;
        text-align: center;
        border-radius: 4px;
        opacity: 0;
        &--active{
            animation: success 2s ease-in-out ;
        }
        &--error{
            background-color: rgb(207, 64, 64);
            animation: success 2s ease-in-out ;
        }
    }
}

@keyframes success {
    0%{
        opacity: 0;
    }
    20%{
        opacity: 1;
    }
    80%{
        opacity: 1;
    }
    100%{
        opacity: 0;
    }
}