.tags{
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 30px;
    &__tag{
        margin-left: 6px;
        &:first-of-type{
            margin-left: 0;
        }
        input{
            display: none;
        }
        label{
            padding: 4px 8px;
            border: 1px solid #007bff;
            border-radius: 4px;
            color: #007bff;
            cursor: pointer;
        }
        input:checked + label{
            background-color: #007bff;
            color: #fff;
        }
    }
}